<script>
// @ is an alias to /src
import NavBar from '@/components/NavBar.vue';
import generalService from '../services/general-service';
import logger from '@/services/logger.js';

export default {
  name: 'Tos',
  components: {
    NavBar
  },
  created() {
    logger.debug(logger);
    logger.error('Boo');
  },
  methods: {
    acceptTos: async function() {
      let token = this.$store.getters.getToken;
      let response = await generalService.sendTosConfirmation(token);
      if (response.success) {
        this.$router.push('/Teacher');
      } else {
        logger.debug('Accept TOS req failed.');
      }
    },
    declineTos: function() {
      logger.debug('User declined TOS.');
      this.$store.dispatch('cleanState');
      this.$router.push('/');
    }
  }
};
</script>

