}

.buttons-below-terms button {
  border: none;
  padding: 0.5rem 2rem 0.5rem 2rem;
  border-radius: 25px;
  font-size: 14pt;
  margin: 10px;
  box-shadow: 2px 2px 15px -5px black;
  outline: 0;
  cursor: pointer;
}

button:hover {
  box-shadow: 2px 2px 20עpx -4px black;
}

#white-btn {
  background-color: white;
}

button:active {
  transform: translateY(3px);
}

@media (max-width: 720px) {
  .details {
